body {
  margin: 0;
  font-family: sans-serif;
  background: #f5f5f5;
}

.auth-container {
  max-width: 400px;
  margin: 80px auto;
  padding: 2rem;
  background: white;
  border-radius: 8px;
  box-shadow: 0 0 10px #ddd;
  text-align: center;
}

.auth-form input {
  width: 100%;
  padding: 0.75rem;
  margin: 0.5rem 0;
  font-size: 1rem;
}

.auth-form button {
  padding: 0.75rem;
  width: 100%;
  background: #1e88e5;
  color: white;
  font-weight: bold;
  border: none;
  cursor: pointer;
  margin-top: 1rem;
}

.auth-form button:hover {
  background: #1565c0;
}

.auth-container p {
  margin-top: 1rem;
  cursor: pointer;
  color: #1e88e5;
  font-size: 0.9rem;
}

.error {
  color: red;
  font-size: 0.85rem;
}

.board-container {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  padding: 1rem;
}

.columns {
  display: flex;
  gap: 1rem;
  justify-content: space-between;
  flex-wrap: wrap;
}

.column {
  flex: 1;
  min-width: 280px;
  background: #e3f2fd;
  padding: 1rem;
  border-radius: 8px;
}

.task-card {
  background: white;
  padding: 1rem;
  margin: 0.5rem 0;
  border-radius: 6px;
  box-shadow: 0 0 5px #ccc;
  cursor: grab;
  transition:
    transform 0.3s cubic-bezier(0.4, 0.2, 0.2, 1),
    box-shadow 0.3s;
}

.task-card:active {
  transform: scale(1.04) rotate(-2deg);
  box-shadow: 0 8px 24px #90caf9;
}

.task-card.flipped {
  transform: rotateY(180deg);
  background: #e3f2fd;
  color: #1565c0;
}

.task-card h4 {
  margin: 0 0 0.3rem;
}

.task-card .task-actions {
  margin-top: 0.5rem;
  display: flex;
  gap: 0.5rem;
  justify-content: flex-end;
}

.task-card button {
  padding: 0.3rem 0.6rem;
  font-size: 0.8rem;
  border: none;
  background: #2196f3;
  color: white;
  cursor: pointer;
  border-radius: 4px;
}

.task-card .danger {
  background: #e53935;
}

.log-panel {
  margin-top: 1rem;
  padding: 1rem;
  background: #fff3e0;
  border-radius: 8px;
  max-height: 200px;
  overflow-y: auto;
  font-size: 0.9rem;
}

.log-panel h3 {
  margin-top: 0;
}

.log-panel ul {
  list-style: none;
  padding-left: 0;
}

.log-panel li {
  margin-bottom: 0.4rem;
  border-bottom: 1px solid #ffe0b2;
  padding-bottom: 0.3rem;
}

.timestamp {
  color: #999;
  font-size: 0.8rem;
  margin-right: 0.5rem;
}

.add-task-btn {
  margin: 1rem 0;
  padding: 0.6rem 1rem;
  font-size: 1rem;
  background: #4caf50;
  color: white;
  border: none;
  border-radius: 6px;
  cursor: pointer;
}

.modal-backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0,0,0,0.6);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 999;
}

.modal {
  background: white;
  padding: 1.5rem;
  border-radius: 8px;
  min-width: 300px;
  max-width: 90%;
  box-shadow: 0 0 10px #222;
}

.modal input, .modal textarea, .modal select {
  width: 100%;
  margin-bottom: 0.8rem;
  padding: 0.6rem;
  font-size: 1rem;
}

.modal-actions {
  display: flex;
  justify-content: flex-end;
  gap: 1rem;
}

.error {
  color: red;
  font-size: 0.9rem;
}


.conflict-section pre {
  background: #f1f1f1;
  padding: 0.6rem;
  overflow-x: auto;
  font-size: 0.85rem;
  border-radius: 4px;
}

@media (max-width: 800px) {
  .columns {
    flex-direction: column;
    gap: 1.5rem;
  }
  .column {
    min-width: 90vw;
  }
  .board-container {
    padding: 0.5rem;
  }
}

@media (max-width: 500px) {
  .auth-container {
    margin: 20px auto;
    padding: 1rem;
  }
  .modal {
    min-width: 90vw;
    padding: 1rem;
  }
}
